.card:nth-child(1) .front-card{
    background: linear-gradient(rgba(255, 255, 255, 0.1), rgba(0, 0, 0, 0.5)), url(/static/media/Prachi.2aaff216.jpeg) center/cover no-repeat;
}
.card:nth-child(2) .front-card{
    background: linear-gradient(rgba(255, 255, 255, 0.1), rgba(0, 0, 0, 0.5)), url(/static/media/Abhishek.c7adfedb.jpeg) center/cover no-repeat;
}
.card:nth-child(3) .front-card{
    background: linear-gradient(rgba(255, 255, 255, 0.1), rgba(0, 0, 0, 0.5)), url(/static/media/Anshul.d5ac904f.jpeg) center/cover no-repeat;
  
}
.card:nth-child(4) .front-card{
    background: linear-gradient(rgba(255, 255, 255, 0.1), rgba(0, 0, 0, 0.5)), url(/static/media/Ashish.f01fa186.jpeg) center/cover no-repeat;
  
}
.card{
    height: 450px;
    -webkit-perspective: 1000px;
            perspective: 1000px;
}
.card-wrapper{
    position: relative;
    width: 100%;
    height: 100%;
    transition: -webkit-transform 1s ease;
    transition: transform 1s ease;
    transition: transform 1s ease, -webkit-transform 1s ease;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    box-shadow: 10px 20px 40px 10px rgba(0, 0, 0, 0.5);
}
.card:hover .card-wrapper{
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
.front-card, .rear-card{
    border-radius: 4px;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
}
.rear-card{
    text-align: center;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    background: #01BF71;
    color: #fff;
    padding: 20px;
}
.front-card div{
    position: absolute;
    bottom: 15px;
    left: 15px;
    color: #fff;
    line-height: 1.6;
}
.rear-card p{
    line-height: 1.6;
    font-size: 15px;
    font-weight: 300;
}
.about-info{
    margin-top: 18px;
}
.about-info div h2{
    font-size: 19px;
    margin-bottom: 2em;
    padding: 10px 0 2px;
}







.wave {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background: rgba(1, 191, 113, 0.8);
}
.wave span {
    position: absolute;
    width: 325vh;
    height: 325vh;
    top: 0;
    left: 50%;
    -webkit-transform: translate(-50%,-75%);
            transform: translate(-50%,-75%);
    background: #fff;
}
.wave span:nth-child(1){
    -webkit-animation: animate 5s linear infinite;
            animation: animate 5s linear infinite;
    border-radius: 45%;
    background: rgba(255, 255, 255, 1);
}
.wave span:nth-child(2){
    -webkit-animation: animate 10s linear infinite;
            animation: animate 10s linear infinite;
    border-radius: 40%;
    background: rgba(255, 255, 255, 0.2);
}
.wave span:nth-child(3){
    -webkit-animation: animate 15s linear infinite;
            animation: animate 15s linear infinite;
    border-radius: 42.5%;
    background: rgba(255, 255, 255, 0.5);
}
@-webkit-keyframes animate {
    0%
    {
        -webkit-transform: translate(-50%,-75%) rotate(0deg);
                transform: translate(-50%,-75%) rotate(0deg);
    }
    100%
    {
        -webkit-transform: translate(-50%,-75%) rotate(360deg);
                transform: translate(-50%,-75%) rotate(360deg);
    }
}
@keyframes animate {
    0%
    {
        -webkit-transform: translate(-50%,-75%) rotate(0deg);
                transform: translate(-50%,-75%) rotate(0deg);
    }
    100%
    {
        -webkit-transform: translate(-50%,-75%) rotate(360deg);
                transform: translate(-50%,-75%) rotate(360deg);
    }
}





@media(max-width: 1170px){
    .wrapper{
        padding: 30px 0;
    }
